  /* Add this CSS in your tracker.css file or in a separate CSS file */
.toggle-container {
  position: relative;
  display: inline-block;
  padding-top: 5%;
}

.toggle-container input[type="checkbox"] {
  opacity: 0;
  width: 0;
  height: 0;
}

.toggle-label {
  display: inline-block;
  cursor: pointer;
  position: relative;
  width: 3vw;
  padding: 0.8vw;
  border-radius: 10vw;
  background-color: #ddd;
}

.toggle-text {
  font-size: 1vw;
  color: #666;
  text-transform: uppercase;
}

.toggle-label::before {
  content: "";
  position: absolute;
  top: 15%;
  left: 10%;
  width: 1vw;
  height: 1vw;
  border-radius: 50%;
  background-color: #fff;
  transition: transform 0.2s ease;
}

.toggle-container input[type="checkbox"]:checked + .toggle-label::before {
  transform: translateX(2.5vw);
}

.toggle-container input[type="checkbox"]:checked + .toggle-label {
  background-color: #4caf50;
}

.show-text {
  padding: 4%;
}